import i18n from 'i18next';
import Backend from 'i18next-http-backend';
import {
  initReactI18next
} from 'react-i18next';

export default i18n
  .use(Backend)
  .use(initReactI18next)
  .init({
    backend: {
      loadPath: process.env.PUBLIC_URL + "/locales/{{lng}}/content.json"
    },
    fallbackLng: 'en',
    interpolation: {
      escapeValue: false,
    },
  });